
import store from '@/store'
import AppLike from './AppLike.vue'

export default {
  name: 'AppPost',
  data: () => {
    return {
      isOpenP: false,
      isLiked: false,
      isDisliked: false
    }
  },
  props: ['post', 'id'],
  methods: {
    updateLoaders() {
      store.dispatch('updateAllLoaders')
    }
  },
  components: { AppLike }
}

